













































































import { Vue, Component } from 'vue-property-decorator';

import { ETableSorting, ETableRowType, EPageStep, EPageAction } from '@/../enums';
import { ServiceDatetime, ServiceResponseFilter, ServiceServerConfig, ServiceResolveText } from '@/../helpers';
import { IComponent } from '@/../models';

import { ServerApiPath } from '@/config';

import Loading from '@/../components/loading/loading.vue';

import Table from '@/../components/table/table.vue';

import Card from '@/../components/card/card.vue';
import DetailTable from './detail-table.vue';

interface ITableData {
    objectId: string;
    date: string;
    count: string;
    companyName: string;
}

interface ITableApiParam {
    paging: {
        page: number;
        pageSize: number;
    };
    sorting?: {
        field?: string;
        order?: number;
    };
}

interface IFormItem {
    isClearState: boolean;
    isSearch: boolean;
}

interface IFormData {
    objectId: string;
    date: string;
    companyName: string;
}

@Component({
    components: { Table, Card, DetailTable },
})
export default class VuePageClass extends Vue {
    private tableItem: IComponent.ITableItem = {
        path: ServerApiPath.AuditPersonStaffSummary_CRUD,
        showPaging: true,
        showCheckbox: false,
        checkboxDisabled: true,
        datas: [],
        columns: [],
        paging: {
            pageSize: 10,
            page: 1,
            totalPages: 0,
            total: 0,
        },
        actions: {
            disableEdit: true,
            disablePerview: false,
            disableDelete: true,
        },

        // Option: only default sorting
        sorting: {
            field: undefined,
            orderEnum: ETableSorting.desc,
            order: undefined,
        },

        // // Option: change conditions
        // conditions: [5, 10, 25, 50, 100],
        // condition: 50,
    };

    private tableParam: IComponent.ITableParam = {
        page: 1,
        pageSize: 10,
        sortingField: undefined,
        sortingOrderEnum: ETableSorting.none,
        sortingOrder: undefined,
    };

    private tableApiParam: ITableApiParam = {
        paging: {
            page: 1,
            pageSize: 10,
        },
        sorting: {},
    };

    private tableSelectedData: ITableData[] = [];

    private pageItem: IComponent.IPageItem = {
        page: EPageStep.table,
        action: EPageAction.create,
    };

    private formItem: IFormItem = {
        isClearState: false,
        isSearch: false,
    };

    private formData: IFormData = {
        objectId: '',
        date: '',
        companyName: '',
    };

    private loading = new Loading();

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initTable();
        await this.initSelectOption();
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// page ////////////////////

    private pageTable(): boolean {
        return this.pageItem.page === EPageStep.table;
    }

    private pageForm(): boolean {
        return this.pageItem.page === EPageStep.form;
    }

    private pageToList(): void {
        this.pageItem.page = EPageStep.table;
        this.pageItem.action = EPageAction.list;
    }

    private async pageToPreview(rowData: ITableData): Promise<void> {
        this.formData = rowData;
        this.pageItem.page = EPageStep.form;
        this.pageItem.action = EPageAction.preview;
    }

    //////////////////// Init Table ////////////////////

    private initTableColumns(): void {
        this.tableItem.columns = [
            { title: this.$i18n.Common_NO, type: ETableRowType.autoIndex },
            { title: this.$i18n.Date_Date, key: 'date', sort: true },
            { title: 'Count', key: 'count', sort: true },
        ];
    }

    private initTableParam() {
        this.tableParam = {
            page: this.tableItem.paging.page,
            pageSize: this.tableItem.paging.pageSize,
            sortingField: this.tableItem.sorting && this.tableItem.sorting.field ? this.tableItem.sorting.field : '',
            sortingOrderEnum: this.tableItem.sorting && this.tableItem.sorting.orderEnum ? this.tableItem.sorting.orderEnum : ETableSorting.none,
            sortingOrder: this.tableItem.sorting && this.tableItem.sorting.order !== undefined ? this.tableItem.sorting.order : undefined,
        };
    }

    private async initTable(): Promise<void> {
        this.initTableColumns();
        this.initTableParam();
        await this.tableReload(this.tableParam);
    }
    //////////////////// Init Data ////////////////////

    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {}

    //////////////////// Table ////////////////////
    private async tableGetApiData(tableParam: IComponent.ITableParam = undefined): Promise<void> {
        if (tableParam && tableParam.page && tableParam.pageSize) {
            this.tableItem.paging.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableItem.paging.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;

            this.tableApiParam.paging = {
                page: this.tableItem.paging.page,
                pageSize: this.tableItem.paging.pageSize,
            };
        }

        if (tableParam && tableParam.sortingField && tableParam.sortingOrder !== undefined) {
            this.tableApiParam.sorting = {
                field: tableParam.sortingField,
                order: tableParam.sortingOrder,
            };
        } else {
            delete this.tableApiParam.sorting;
        }

        this.loading.show();

        let data = this.getFakeData(tableParam);
        this.tableSetData(data);

        this.loading.hide();
    }

    private getFakeData(tableParam: IComponent.ITableParam): any {
        let data: any[] = JSON.parse(JSON.stringify(REPORT_AUDIT_STAFF_SUMMARY));

        // sorting
        if (!!tableParam && !!tableParam.sortingField && tableParam.sortingOrderEnum !== ETableSorting.none) {
            let sortingField: string = tableParam.sortingField;
            data = data.sort((a, b) => {
                if (tableParam.sortingOrder === 0) {
                    if (typeof a[sortingField] === 'number') {
                        return a[sortingField] - b[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return 1;
                    if (a[sortingField] < b[sortingField]) return -1;
                } else {
                    if (typeof a[sortingField] === 'number') {
                        return b[sortingField] - a[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return -1;
                    if (a[sortingField] < b[sortingField]) return 1;
                }

                return 0;
            });
        }

        // pagination
        let pageSize: number = tableParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        return data;
    }

    private async tableReload(tableParam: IComponent.ITableParam): Promise<void> {
        if (tableParam) {
            this.tableParam.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableParam.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;
            this.tableParam.sortingField = tableParam.sortingField;
            this.tableParam.sortingOrder = tableParam.sortingOrder;

            await this.tableGetApiData(this.tableParam);
        }
    }

    private tableSetData(result: any[]): void {
        let tableData: ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                objectId: element.objectId ? element.objectId : '',
                date: ServiceResolveText.resolveDate(element.date),
                count: element.count ? element.count : null,
                companyName: element.companyName ? element.companyName : '',
            });
        });

        this.tableItem.datas = tableData;
    }

    private titleDetailTitle(): string {
        return `${this.formData.date} ${this.formData.companyName} Detail Table`;
    }

    //////////////////// Form Show ////////////////////
    private formTitle(title: string): string {
        return title ? title : this.$i18n.Form_FilterCreateNewData;
    }

    //////////////////// Form Set Data ////////////////////

    private async formDataSet(data: ITableData): Promise<void> {
        this.formData.objectId = data.objectId;
        this.formData.companyName = data.companyName;
        this.formData.date = data.date;

        this.formDataSet(data);
    }
    //////////////////// Modify API (save, delete, test)  ////////////////////

    //////////////////// Resolve text ////////////////////
}

/// Fake Data
